  // to get only number in AMOUNT field
  numberOnlyValidation(event: any) {
    const pattern = /[0-9.,]/;
    let inputChar = String.fromCharCode(event.charCode);

    if (!pattern.test(inputChar)) {
      // invalid character, prevent input
      event.preventDefault();
    }
  }

  expFormat(val) {
    if (val.length === 2 && val[val.length - 1] !== "/") {
      this.exp = val + "/";
    }
  }

  verifyRecaptcha() {
    this.loader.presentLoading().then(() => {
      let current_url = new URL(window.location.href);
      this.recaptchaV3Service.execute("payment_giveNew").subscribe(
        (token) => {
          if (token) {
            this.contribution.checkCaptcha(token).subscribe(
              (res: any) => {
                this.loader.stopLoading();
                if (res && res.status && res.message) {
                  let resp = JSON.parse(res.message);
                  if (resp.success === true && resp.score > 0.3) {
                    this.createPayment(false);
                  } else if (resp.success === true && resp.score <= 0.3) {
                    this.clearAllFields();
                    this.toast.presentToastWithOptions(
                      "User not Verified by reCaptcha, Enter Details again!",
                      "ReCaptcha Alert!",
                      "warning"
                    );
                  } else if (resp.success === false) {
                    this.toast.presentToastWithOptions(
                      "Site Not Verified by reCaptcha",
                      "ReCaptcha Alert!",
                      "warning"
                    );
                  }
                } else {
                  this.toast.presentToastWithOptions(
                    "Site Not Verified by reCaptcha!",
                    "ReCaptcha Alert!",
                    "warning"
                  );
                }
              },
              (err: any) => {
                this.loader.stopLoading();
                this.toast.presentToastWithOptions(
                  "Error while verifying by reCaptcha!",
                  "Error",
                  "warning"
                );
              }
            );
          } else {
            this.loader.stopLoading();
            this.toast.presentToastWithOptions(
              "Token not found by reCaptcha!",
              "ReCaptcha Alert!",
              "warning"
            );
          }
        },
        (error) => {
          this.loader.stopLoading();
          this.createPayment(true);
        }
      );
    });
  }

